.App {
  text-align: center;
  height: 100%;
  width: 100%;
}

.map {
  height: 100vh;
  width: 100%;
}

.clicked-coord-label {
  position: absolute;
  right: 0;
  bottom: 0;
  background: white;
  border-radius: 5px;
}

.bot-bar {
  position: absolute;
  bottom: 0;
  z-index: 1;
}

.coords-input {
  margin: 10px;
}

.coord-trans-butt {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.item-head {
  display: flex;
}
.prim {
  flex-grow: 15;
  font-size: 1rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.00938em;
}

.quick-icons {
  flex-grow: 1;
}

.secnd {
  display: block;
  color: rgba(0, 0, 0, 0.54);
  font-size: 0.875rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.43;
  letter-spacing: 0.01071em;
  margin: 0;
}
